import "./App.css";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from "./component/Home/Home";
import Dogs from "./component/Dogs/Dogs";
import Cart from "./component/Cart/Cart";
import Navbar from "./component/Navbar/Navbar";
import { useEffect, useState } from "react";
import axios from "axios";
import { CartContext } from "./Context/CartContext";

function App() {
  const [allDogs, setAllDogs] = useState([]);
  const [myCart, setMyCart] = useState([{}]);
  const [total, setTotal] = useState(0);
  useEffect(() => {
    function getData() {
      const res = axios.get("https://61ab7ddf264ec200176d41e8.mockapi.io/item");
      return res;
    }
    getData().then((res) => setAllDogs(res.data));
    getData().catch((err) => console.log(err));
  }, []);
  return (
    <CartContext.Provider value={{ myCart, setMyCart, total, setTotal }}>
      <Router>
        <Navbar />
        <div className="page-containter">
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/Shop_Dog" element={<Home />} />
            <Route path="/dogs" element={<Dogs allDogs={allDogs} />} />
            <Route path="/cart" element={<Cart />} />
          </Routes>
        </div>
      </Router>
    </CartContext.Provider>
  );
}

export default App;
